<template>
  <div>
    <label class="form-label fw-400 text-black-50" :for="data.id">{{data.label}} <i class="fal fa-fw fa-asterisk text-danger" v-show="data.validate.required"></i></label>
    <div class="input-group">
      <div class="custom-file">
        <input
          type="file"
          class="custom-file-input"
          :id="data.id"
          :class="{'is-invalid': data.invalid}"
          :aria-describedby="'btn'+data.id"
          @change="changeFile($event)"
        />
        <label class="custom-file-label" :for="data.id">{{name||"选择文件"}}</label>
      </div>
      <div class="input-group-append">
        <button
          class="btn btn-outline-default waves-effect waves-themed"
          type="button"
          :class="{'disabled':!name}"
          @click="upload($event)"
          :id="'btn'+data.id"
        >上传</button>
      </div>
    </div>

    <ul class="list-group shadow-sm" v-for="(file,index) in files" :key="file.path">
      <li class="list-group-item d-flex justify-content-between align-items-center">
        {{file.name}}
        <a
          href="javascript:void(0);"
          @click="removeFile(index)"
          class="badge badge-danger badge-pill"
        >
          <i class="far fa-fw fa-times mr-2"></i>删除
        </a>
      </li>
    </ul>
    <!-- <ul class="list-group shadow-sm">
      <li class="list-group-item d-flex justify-content-between align-items-center">
        Cras justo odio
        <a href="#" class="badge badge-danger badge-pill">
          <i class="far fa-fw fa-times mr-2"></i>删除
        </a>
      </li>
    </ul>-->
  </div>
</template>


<script>
import config from "@/common/config";
import utils from "@/common/utils.js";
import service from "@/common/service";
import moment from "moment";
export default {
  data() {
    return utils.createModel({
      name: "",
      files: [],
    });
  },
  props: ["data"],
  created() {
    if (!this.data.value) {
      this.data.value = "[]";
    }
    var files = JSON.parse(this.data.value);
    files.forEach((file) => {
      this.files.push(file);
    });
  },
  mounted() {},
  methods: {
    changeFile(event) {
      var file = event.target.files[0];
      var name = (file && file.name) || "";
      this.name = name;
    },
    upload(event) {
      var field = $(event.target).closest(".input-group").find("input");
      var files = field[0].files;
      if (!files || files.length === 0) {
        return;
      }
      var formData = new FormData();
      formData.append("fileUploaded", files[0]);
      formData.append("user", this.user.id);
      formData.append("id", "lpa");
      service.upload(formData).then((response) => {
        this.files.push(response.data);
        field.val("");
        this.name = "";
        this.data.value = JSON.stringify(this.files);
      });
    },
    removeFile(index) {
      //   var items = this.data.value;
      //  items.splice(index, 1);

      this.$delete(this.files, index);
      this.data.value = JSON.stringify(this.files);
    },

    // fileUploaded
    // user
    //  id
  },
};
</script>

